.button {
  display: inline-block;
  height: 53px;
  border-radius: 27px;
  overflow: hidden;
  z-index: 1;
  margin-bottom: 10px;
}

.button_bcg {
  position: absolute;
  top: 0;
  left: 0;
  width: 200%;
  height: 100%;
  background: -webkit-gradient(linear, left top, right top, from(#fa9e1b), color-stop(#8d4fff), to(#fa9e1b));
  background: -webkit-linear-gradient(left, #fa9e1b, #8d4fff, #fa9e1b);
  background: -o-linear-gradient(left, #fa9e1b, #8d4fff, #fa9e1b);
  background: linear-gradient(to right, #fa9e1b, #8d4fff, #fa9e1b);
  z-index: -1;
  -webkit-transition: all 400ms ease;
  -o-transition: all 400ms ease;
  transition: all 400ms ease;
}

.button:hover .button_bcg {
  left: -100%;
}

.button a {
  display: block;
  font-size: 13px;
  font-weight: 700;
  color: #fff;
  text-transform: uppercase;
  line-height: 53px;
  padding-left: 46px;
  padding-right: 46px;
  white-space: nowrap;
  z-index: 2;
}

.button a:hover {
  cursor: pointer;
}

.button span {
  display: inline-block;
  vertical-align: middle;
  width: 5.75px;
  height: 5.63px;
  background: #FFFFFF;
  border-radius: 50%;
  margin-right: 2px;
}

.button span:first-child {
  margin-left: 10px;
}

.button span:nth-child(2) {
  opacity: 0.6;
}

.button span:last-child {
  opacity: 0.4;
  margin-right: 0px;
}



