  padding-top: 10px;
}

.main_nav_item::after {
  display: block;
  position: absolute;
  left: -1px;
  bottom: -11px;
  width: calc(100% + 2px);
  height: 2px;
  background: -webkit-gradient(linear, left top, right top, from(#fa9e1b), to(#8d4fff));
  background: -webkit-linear-gradient(left, #fa9e1b, #8d4fff);
  background: -o-linear-gradient(left, #fa9e1b, #8d4fff);
  background: linear-gradient(to right, #fa9e1b, #8d4fff);
  content: '';
  opacity: 0;
  -webkit-transition: all 200ms ease;
  -o-transition: all 200ms ease;
  transition: all 200ms ease;
}

.main_nav_item:hover::after {
  opacity: 1;
}

.content_search {
