  height: 100vh;
  background: -webkit-gradient(linear, left top, right top, from(#fa9e1b), to(#8d4fff));
  background: -webkit-linear-gradient(left, #fa9e1b, #8d4fff);
  background: -o-linear-gradient(left, #fa9e1b, #8d4fff);
  background: linear-gradient(to right, #fa9e1b, #8d4fff);
  z-index: 100;
  opacity: 0;
  visibility: hidden;
}

.menu.active {
  opacity: 0.95;
  visibility: visible;
}

.menu_content {
  width: 100%;
  height: 100%;
}

.menu_item {
  position: relative;
  margin-bottom: 3px;
}

.menu_item:last-child {
  margin-bottom: 0px;
}

.menu_logo {
  margin-bottom: 28px;
}

.menu_logo a img {
