}

.menu_item a {
  display: inline-block;
  position: relative;
  font-family: 'Beyond';
  font-size: 36px;
  color: #FFFFFF;
  font-weight: 400;
}

.menu_item a::after {
  display: block;
  position: absolute;
  top: 60%;
  left: 0;
  width: 0;
  height: 4px;
  background: #FFFFFF;
  letter-spacing: 2px;
  content: '';
  z-index: 10;
  pointer-events: none;
  -webkit-transition: all 200ms ease;
  -o-transition: all 200ms ease;
  -webkit-transition: all 400ms ease;
  -o-transition: all 400ms ease;
  transition: all 400ms ease;
}

.menu_item:hover a::after {
  width: 100%;
}

.menu_close_container {
  position: absolute;
  top: 94px;
  right: 122px;
  width: 21px;
  height: 21px;
  cursor: pointer;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.menu_close {
  top: 9px;
  width: 21px;
  height: 3px;
  background: #FFFFFF;
  -webkit-transition: all 200ms ease;
  -o-transition: all 200ms ease;
  transition: all 200ms ease;
}

.menu_close::after {
  display: block;
  position: absolute;
  top: -9px;
  left: 9px;
  content: '';
  width: 3px;
  height: 21px;
  background: #FFFFFF;
  -webkit-transition: all 200ms ease;
  -o-transition: all 200ms ease;
  transition: all 200ms ease;
}

.menu_close_container:hover .menu_close, .menu_close_container:hover .menu_close::after {
  background: #fa9e1c;
}

