.contact_info_icon img {
  display: block;
  width: 100%;
}

.contact_info_text, .contact_info_text a {
  color: #FFFFFF;
  line-height: 2.14;
  margin-top: -4px;
}

.contact_info_item:hover .contact_info_text, .contact_info_item:hover .contact_info_text a {
  color: #fa9e1b;
}

/*********************************
7. Copyright
*********************************/

.copyright {
  background: #1f0733;
}

.copyright_content {
  height: 52px;
}

.copyright_content div {
  font-size: 12px;
  font-weight: 600;
  color: #685677;
}

.footer_nav_container {
  height: 52px;
}

.footer_nav {}

.footer_nav_list {
  font-size: 0px;
}

.footer_nav_item {
  display: inline-block;
  margin-right: 40px;
}

.footer_nav_item a {
  font-size: 12px;
  font-weight: 600;
  color: #685677;
  text-transform: uppercase;
  line-height: 2;
  -webkit-transition: all 200ms ease;
  -o-transition: all 200ms ease;
  transition: all 200ms ease;
}

.footer_nav_item a:hover {
  color: #FFFFFF;
}



